/*-------------------------------------------*/

/*	embed card
/*-------------------------------------------*/

p.wp-embed-heading {
	font-size: 1.25em;
	font-weight: 500;
}

.wp-embed-featured-image {
	float: right;
	position: relative;
	margin-left: 1em;
	width: 40%;
	overflow: hidden;
}

.wp-embed-site-icon {
	height: 20px;
	width: 20px;
}

.wp-embed-site-title {
	font-size: 0.9em;
	line-height: 1.4;
	font-weight: 500;
}

.wp-embed-site-title a {
	padding-left: 30px;
}

@media screen and (min-width: 481px) {

.wp-embed-featured-image {
	width: 25%;
}

}

@media screen and (max-width: 1024px) {

.wp-embed-excerpt {
	overflow: auto;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
	overflow: hidden;
}

}

@media screen and (max-width: 480px) {

p.wp-embed-heading {
	font-size: 1.1em;
}

.wp-embed {
	padding: 15px;
}

}

